import React, { Component } from 'react';
import { Row, Col, Form, Radio } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate/index';
import { BtnSearch, BtnClear } from 'components/common/button';
import { SelectAjax } from 'components/common/index';
import { getYearEducations } from 'services/yearEducationService';
import { getSemesters } from 'services/semesterService';
import './style.scss';

@Form.create({ name: 'FormSearchClasses' })
class FormSearchStudent extends Component {
  state = {};

  getSubjectTypes = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'A',
          
        },
        {
          id: '2',
          label: 'B',
          
        },
        {
          id: '3',
          label: 'C',
          
        },
  
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 3,
    };
    const res = {
    data,
    };
    return res; 
  }

  getBatches = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '1',
          
        },
        {
          id: '2',
          label: '2',
          
        },
        {
          id: '3',
          label: '3',
          
        },
  
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 3,
    };
    const res = {
    data,
    };
    return res; 
  }

  getReceipts = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'A',
          
        },
        {
          id: '2',
          label: 'B',
          
        },
        {
          id: '3',
          label: 'C',
          
        },
  
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 3,
    };
    const res = {
    data,
    };
    return res; 
  }

  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      this.props.dataForm(values);
      console.log('Received values of form: ', values);
    });
  };

  render() {
    const { form: { getFieldDecorator, getFieldValue, setFieldsValue } } = this.props;
    const yearEduId = getFieldValue('yearEduId');
    return (
      <Form className="form-inline-label search-form" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24} type='flex' align='stretch'>
          <FilterPathEducate form={this.props.form} />
          <Col span={6}>
            <Form.Item label="Năm học">
              {getFieldDecorator('yearEduId', {})(
                <SelectAjax
                  service={getYearEducations}
                  setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Học kỳ">
              {getFieldDecorator('semesterId', {})(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getSemesters}
                  params={{ yearEduId }}
                  allowGetData={!!yearEduId}
                />
              )}
            </Form.Item>  
          </Col>
          <Col span={6}>
            <Form.Item label="Khoản nộp">
              {getFieldDecorator('receiptType', {})(<SelectAjax service={this.getReceipts} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Hiển thị SV">
              {getFieldDecorator('studentDisplyTypes', {})(<SelectAjax service={this.getReceipts} />)}
            </Form.Item>
          </Col>
          <Col span={6} className="view-type">
            <Form.Item>
              {getFieldDecorator('viewType')(
                <Radio.Group>
                  <Radio value="semester">Theo học kỳ</Radio>
                  <Radio value="course">Toàn khóa học</Radio>
                </Radio.Group>,
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearchStudent;



