import React from 'react';
import { Row, Col } from 'antd';
import './style.scss';

export const StatisticBar = () => (
  <Row gutter={24} className="statistic-bar">
    <Col xs={24} sm={24} md={8} lg={8} xl={8}>
      <Row>
        <Col span={12} className="title">Số tiền phải thu kỳ:</Col>
        <Col span={12} className="result">1,000,000</Col>
        <Col span={12} className="title">Số tiền đã thu kỳ:</Col>
        <Col span={12} className="result">1,000,000</Col>
      </Row>
    </Col>
    <Col xs={24} sm={24} md={8} lg={8} xl={8}>
      <Row>
        <Col span={12} className="title">Số tiền thu thừa kỳ:</Col>
        <Col span={12} className="result">1,000,000</Col>
        <Col span={12} className="title">Số tiền còn phải thu kỳ:</Col>
        <Col span={12} className="result">1,000,000</Col>
      </Row>
    </Col>
    <Col xs={24} sm={24} md={8} lg={8} xl={8}>
      <Row>
        <Col span={12} className="title">Số tiền thu thừa cuối kỳ:</Col>
        <Col span={12} className="result">1,000,000</Col>
        <Col span={12} className="title">Số tiền còn phải thu cuối cùng:</Col>
        <Col span={12} className="result">1,000,000</Col>
      </Row>
    </Col>
  </Row>
);



